*{
    margin: 0;padding: 0;box-sizing: border-box;
}

.maincon{
    height: 100vh;width: 100vw;
    background-color: rgb(36, 36, 36);
    display: flex;
    align-items: center;
    justify-content: center;
}

.centercon{
    height: 500px;
    width: 400px;
    /* border: 1px solid white; */
    display: flex;
    flex-direction: column;
}
.fybox{
    
    height: 60px;width: 400px;display: flex;justify-content: center;
}
.inp{
    border: none;
    outline: none;
    height: 45px;
    width: 350px;
    border-radius: 15px;font-size: large;
    box-shadow: 0px 10px 20px rgba(0,0,0,0.3);
    text-align: center;
}
.addbtn{
    height: 45px;width: 45px;border-radius: 30px;
    background-color: rgb(59, 67, 183);margin-left: 10px;display: flex;align-items: center;
    justify-content: center;font-weight: bolder;font-size: 26px;color: white;
}

.fy{
    margin-bottom: 37px;
}
.addbtn:hover{
    background-color: blue;
    cursor: pointer;
}
