.mainbox{
    color: white;
    height: 40px;
    width: 400px;
    border: 2px solid white;
    border-radius: 6px;
    display: flex;align-items: center;justify-content: space-between;
    padding-left: 8px;
    margin-bottom: 5px;
}

.deletbtn{
padding-right: 10px;
}

.deletbtn i:hover{
    color: rgb(255, 100, 100);
    transform: scale(1.2);
    transition: transform 0.3s ease;
    cursor: pointer;
}